// 任意电路
<template>
  <div>
    <el-input class="input-box" v-model.number="num" placeholder="节点个数"></el-input>
    <br />
    <el-input class="input-box" v-model.number="hz" placeholder="频率">
      <template slot="append">hz</template>
    </el-input>
    <br />
    <el-button @click="append">增加边数</el-button>
    <br />
    <div style="width:800px;margin:0 auto">
      <el-form v-model="sides">
        <el-table :data="sides" style="width: 100%">
          <el-table-column type="index" label="元器序号" min-width="20" align="center"></el-table-column>
          <el-table-column label="类型" width="130" align="center">
            <template slot-scope="scope">
              <el-form-item :prop="'sides.' + scope.$index + '.type'">
                <el-select v-model="scope.row.type" placeholder="类型">
                  <el-option
                    v-for="i in typeoptions"
                    :key="i.value"
                    :label="i.label"
                    :value="i.value"
                  ></el-option>
                </el-select>
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column label="起始节点" width="150" align="center">
            <template slot-scope="scope">
              <el-form-item :prop="'sides.' + scope.$index + '.from'">
                <el-select v-model="scope.row.from" placeholder="起始节点">
                  <el-option v-for="i in options" :key="i.value" :label="i.label" :value="i.value"></el-option>
                </el-select>
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column label="终止节点" width="150" align="center">
            <template slot-scope="scope">
              <el-form-item :prop="'sides.' + scope.$index + '.to'">
                <el-select v-model="scope.row.to" placeholder="终止节点">
                  <el-option v-for="i in options" :key="i.value" :label="i.label" :value="i.value"></el-option>
                </el-select>
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column label="值" width="200" align="center">
            <template slot-scope="scope">
              <el-form-item :prop="'sides.' + scope.$index + '.value'">
                <el-input v-model="scope.row.value" placeholder="请输入"></el-input>
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="100" align="center">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="deleteRow(scope.$index, sides)"
                type="text"
                size="small"
              >移除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form>
    </div>
    <el-button @click="run">运算</el-button>
    <div style="width:500px;margin:0 auto">
      <el-table :data="answer" style="width: 100%">
        <el-table-column prop="id" label="节点" width="250" align="center"></el-table-column>
        <el-table-column prop="value" label="电压" align="center"></el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import { create, all } from "mathjs";
const config = {
  epsilon: 1e-12,
  matrix: "Matrix",
  number: "number",
  precision: 64,
  predictable: false,
  randomSeed: null
};
const math = create(all, config);

export default {
  name: "task1",
  data() {
    return {
      num: 4, //边数
      hz: 1, //频率
      sides_number: 4, //边数
      sides: [
        { id: 0, type: "电阻", from: 0, to: 1, value: 1 },
        { id: 1, type: "电容", from: 1, to: 2, value: 1 },
        { id: 2, type: "电感", from: 2, to: 3, value: 1 },
        { id: 3, type: "电流源", from: 0, to: 3, value: 1 }
      ],
      options: [],
      ans: [],
      typeoptions: [
        {
          label: "电压源",
          value: "电压源"
        },
        {
          label: "电流源",
          value: "电流源"
        },
        {
          label: "电感",
          value: "电感"
        },
        {
          label: "电容",
          value: "电容"
        },
        {
          label: "电阻",
          value: "电阻"
        }
      ]
    };
  },
  mounted() {
    let t;
    let a = new Array(3);
    for (let index = 0; index < 3; index++) {
      t = { label: index, value: index };
      a[index] = t;
    }
    this.options = a;
  },
  computed: {
    answer: function() {
      return this.ans.map((item, index) => {
        return {
          id: index + 1,
          value: item[0].re + "+" + item[0].im + "j"
        };
      });
    }
  },
  watch: {
    num: function() {
      /* eslint-disable */
      let t;
      let n = this.num;
      let a = new Array(n);
      for (let index = 0; index < this.num; index++) {
        t = { label: index, value: index };
        a[index] = t;
      }
      this.options = a;
    }
  },
  methods: {
    append: function() {
      this.$set(this.sides, this.sides_number, {
        id: this.sides_number
      });
      this.sides_number += 1;
    },
    deleteRow(index, list) {
      list.splice(index, 1);
    },
    run: function() {
      /* eslint-disable */
      let hz = this.hz;
      let z = math.zeros(this.num, this.num);
      let b = math.zeros(this.num, 1);
      for (let item of this.sides) {
        switch (item.type) {
          case "电阻":
            z._data[item.from][item.from] = math.add(
              z._data[item.from][item.from],
              math.fraction(1, item.value)
            );
            z._data[item.from][item.to] = math.add(
              z._data[item.from][item.to],
              math.fraction(-1, item.value)
            );
            z._data[item.to][item.to] = math.add(
              z._data[item.to][item.to],
              math.fraction(1, item.value)
            );
            z._data[item.to][item.from] = math.add(
              z._data[item.to][item.from],
              math.fraction(-1, item.value)
            );
            break;
          case "电容":
            z._data[item.from][item.from] = math.add(
              z._data[item.from][item.from],
              math.complex(0, hz * item.value)
            );
            z._data[item.from][item.to] = math.add(
              z._data[item.from][item.to],
              math.complex(0, -hz * item.value)
            );
            z._data[item.to][item.to] = math.add(
              z._data[item.to][item.to],
              math.complex(0, hz * item.value)
            );
            z._data[item.to][item.from] = math.add(
              z._data[item.to][item.from],
              math.complex(0, -hz * item.value)
            );
            break;
          case "电感":
            z._data[item.from][item.from] = math.add(
              z._data[item.from][item.from],
              math.complex(0, -1 / (hz * item.value))
            );
            z._data[item.from][item.to] = math.add(
              z._data[item.from][item.to],
              math.complex(0, 1 / (hz * item.value))
            );
            z._data[item.to][item.to] = math.add(
              z._data[item.to][item.to],
              math.complex(0, -1 / (hz * item.value))
            );
            z._data[item.to][item.from] = math.add(
              z._data[item.to][item.from],
              math.complex(0, 1 / (hz * item.value))
            );
            break;
          case "电流源":
            b._data[item.from][0] = math.add(
              b._data[item.from][0],
              math.complex(-item.value, 0)
            );
            b._data[item.to][0] = math.add(
              b._data[item.to][0],
              math.complex(item.value, 0)
            );
            break;
        }
        for (let item of this.sides) {
          if (item.type === "电压源") {
            console.log("bbbbbbbbb");
            for (let i = 0; i < this.num; i++) {
              z._data[item.from][i] = math.add(
                z._data[item.from][i],
                z._data[item.to][i]
              );
              b._data[item.from][0] = math.add(
                b._data[item.from][0],
                b._data[item.to][0]
              );
              z._data[item.to][i] = 0;
              b._data[item.to][0] = 0;
            }
            z._data[item.to][item.to] = 1;
            z._data[item.to][item.from] = -1;
            b._data[item.to][0] = math.complex(item.value, 0);
          }
        }
      }
      console.log(b, z);
      this.ans = math.multiply(
        math.inv(
          z.subset(math.index(math.range(1, this.num), math.range(1, this.num)))
        ),
        b.subset(math.index(math.range(1, this.num), [0]))
      )._data;
    }
  }
};
</script>

<style>
.input-box {
  max-width: 300px;
}
.cell {
  height: 40px;
}
</style>