<template>
  <div>
    <el-tag type="success">rlc并联1</el-tag>
    <br />
    <el-input v-model="L" placeholder="请输入内容" style="width:500px">
      <template slot="append">H</template>
    </el-input>
    <br />
    <el-input v-model="C" placeholder="请输入内容" style="width:500px">
      <template slot="append">F</template>
    </el-input>
    <br />
    <el-input v-model="R" placeholder="请输入内容" style="width:500px">
      <template slot="append">Ω</template>
    </el-input>
    <br />
    <el-button type="primary" @click="drawChart" round>绘图</el-button>
    <div id="main" style="width: 600px;height:400px;margin:0 auto;"></div>
    <!-- <div id="main1" style="width: 600px;height:400px;margin:0 auto;"></div> -->
  </div>
</template>

<script>
export default {
  name: "rlc1",
  data() {
    return {
      R: 100,
      L: 1e-3,
      C: 1e-7
    };
  },
  methods: {
    drawChart() {
      /* eslint-disable */
      let data = [];
      //   let Q = Math.sqrt(this.C / this.L) / this.R;
      for (let i = -100; i < 700; i++) {
        let w = Math.pow(10, i / 100);
        data.push([
          w,
          this.R /
            Math.sqrt(
              Math.pow(this.R, 2) + 1 / Math.pow(w * this.C - 1 / w / this.L, 2)
            )
        ]);
      }
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("main"));
      // let myChart1 = this.$echarts.init(document.getElementById("main1"));
      let op = {
        xAxis: [
          {
            name: "w/w_0",
            nameLocation: "end",
            nameGap: 15,
            type: "log",
            boundaryGap: false,
            axisLabel: {
              //X轴刻度配置
              interval: 5
            },
            axisLine: {
              symbol: ["none", "arrow"], // 分别表示起始位置和终止位置是否使用箭头，默认不使用
              symbolSize: [5, 10] // 箭头的长宽
            }
          }
        ],
        yAxis: {
          name: "U",
          nameLocation: "end",
          nameGap: 15,
          type: "value"
        },
        tooltip: {
          trigger: "item",
          axisPointer: {
            type: "shadow"
          },
          formatter: "x={b}<br>y={c}"
        },
        smooth: true,
        series: [
          {
            name: "U",
            data: data,
            type: "line",
            itemStyle: {
              normal: {
                opacity: 0.8
              }
            }
          }
        ]
      }; // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(op, true);
      // myChart1.setOption(op1, true);
    }
  }
};
</script>