<template>
  <div>
    <el-tag type="success">高通滤波</el-tag>
    <br />
    <el-input v-model="L" placeholder="请输入内容" style="width:500px">
      <template slot="append">H</template>
    </el-input>
    <br />
    <el-input v-model="C" placeholder="请输入内容" style="width:500px">
      <template slot="append">F</template>
    </el-input>
    <br />
    <el-input v-model="R" placeholder="请输入内容" style="width:500px">
      <template slot="append">Ω</template>
    </el-input>
    <br />
    <el-button type="primary" @click="drawChart" round>绘图</el-button>
    <div id="main" style="width: 600px;height:400px;margin:0 auto;"></div>
    <div id="main1" style="width: 600px;height:400px;margin:0 auto;"></div>
  </div>
</template>

<script>
export default {
  name: "rlc1",
  data() {
    return {
      R: 50,
      L: 1e-3,
      C: 1e-5
    };
  },
  methods: {
    drawChart() {
      /* eslint-disable */
      let data = [];
      let data1 = [];
      //   let Q = Math.sqrt(this.C / this.L) / this.R;
      for (let i = -100; i < 600; i++) {
        let w = Math.pow(10, i / 100);
        data.push([
          w,
          (w * this.L) /
            Math.sqrt(
              Math.pow(this.L / this.R / this.C, 2) +
                Math.pow(w * this.L - 1 / this.C / w, 2)
            )
        ]);
        data1.push([
          w,
          (-Math.atan2(-this.L / this.R / this.C, w * this.L - 1 / this.C / w) *
            180) /
            Math.PI
        ]);
      }
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("main"));
      let myChart1 = this.$echarts.init(document.getElementById("main1"));
      let op = {
        xAxis: [
          {
            name: "η",
            nameLocation: "end",
            nameGap: 15,
            boundaryGap: false,
            // data: Array.from({ length: 100 }, (v, k) => (k + 1) / 10),
            axisLine: {
              symbol: ["none", "arrow"], // 分别表示起始位置和终止位置是否使用箭头，默认不使用
              symbolSize: [5, 10] // 箭头的长宽
            },
            type: "log",
            min: 0.1,
            logBase: 10
          }
        ],
        yAxis: {
          name: "U/U_s",
          nameLocation: "end",
          nameGap: 15,
          type: "log"
        },
        tooltip: {
          trigger: "item",
          formatter: "x={b}<br>y={c}"
        },
        smooth: true,
        series: [
          {
            name: "R",
            data: data,
            type: "line",
            itemStyle: {
              normal: {
                opacity: 0.8
              }
            }
          }
        ]
      }; // 使用刚指定的配置项和数据显示图表。
      let op1 = {
        xAxis: [
          {
            name: "η",
            nameLocation: "end",
            nameGap: 15,
            boundaryGap: false,
            // data: Array.from({ length: 100 }, (v, k) => (k + 1) / 10),
            axisLine: {
              symbol: ["none", "arrow"], // 分别表示起始位置和终止位置是否使用箭头，默认不使用
              symbolSize: [5, 10] // 箭头的长宽
            },
            type: "log",
            min: 0.1,
            logBase: 10
          }
        ],
        yAxis: {
          name: "rad",
          nameLocation: "end",
          nameGap: 15,
          type: "value"
        },
        tooltip: {
          trigger: "item",
          formatter: "x={c0}"
        },
        smooth: true,
        series: [
          {
            name: "R",
            data: data1,
            type: "line",
            itemStyle: {
              normal: {
                opacity: 0.8
              }
            }
          }
        ]
      }; // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(op, true);
      myChart1.setOption(op1, true);
    }
  }
};
</script>