<template>
  <div>
    <el-tag type="success">rlc串联</el-tag>
    <br />
    <el-input v-model="L" placeholder="请输入内容" style="width:500px">
      <template slot="append">H</template>
    </el-input>
    <br />
    <el-input v-model="C" placeholder="请输入内容" style="width:500px">
      <template slot="append">F</template>
    </el-input>
    <br />
    <el-input v-model="R" placeholder="请输入内容" style="width:500px">
      <template slot="append">Ω</template>
    </el-input>
    <br />
    <el-button type="primary" @click="drawChart" round>绘图</el-button>
    <div id="main" style="width: 600px;height:400px;margin:0 auto;"></div>
    <div id="main1" style="width: 600px;height:400px;margin:0 auto;"></div>
    <div id="main2" style="width: 600px;height:400px;margin:0 auto;"></div>
  </div>
</template>

<script>
export default {
  name: "rlc1",
  data() {
    return {
      R: 1,
      L: 1,
      C: 1
    };
  },
  methods: {
    drawChart() {
      /* eslint-disable */
      let data = Array.from({ length: 100 });
      let Q = Math.sqrt(this.L / this.C) / this.R;
      for (let i = 0; i < 100; i++) {
        let w = Math.pow((i + 1) / 10, 2);
        data[i] = Math.sqrt(
          Math.pow(this.R, 2) + (this.L / this.C) * (w + 1 / w - 2)
        );
      }
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("main"));
      let myChart1 = this.$echarts.init(document.getElementById("main1"));
      let myChart2 = this.$echarts.init(document.getElementById("main2"));
      let op = {
        title: {
          text: "阻抗",
          x: "center"
        },
        xAxis: [
          {
            name: "w/w_0",
            nameLocation: "end",
            nameGap: 15,
            type: "category",
            boundaryGap: false,
            data: Array.from({ length: 100 }, (v, k) => (k + 1) / 10),
            axisLabel: {
              //X轴刻度配置
              interval: 5
            },
            axisLine: {
              symbol: ["none", "arrow"], // 分别表示起始位置和终止位置是否使用箭头，默认不使用
              symbolSize: [5, 10] // 箭头的长宽
            }
          }
        ],
        yAxis: {
          name: "|Z|",
          nameLocation: "end",
          nameGap: 15,
          type: "value"
        },
        tooltip: {
          trigger: "item",
          axisPointer: {
            type: "shadow"
          },
          formatter: "x={b}<br>y={c}"
        },
        smooth: true,
        series: [
          {
            name: "|Z|",
            data: data,
            type: "line",
            areaStyle: {},
            itemStyle: {
              normal: {
                opacity: 0.8
              }
            }
          }
        ]
      }; // 使用刚指定的配置项和数据显示图表。
      let op1 = {
        title: {
          text: "幅频特性",
          x: "center"
        },
        xAxis: [
          {
            name: "η",
            nameLocation: "end",
            nameGap: 15,
            type: "category",
            boundaryGap: false,
            data: Array.from({ length: 100 }, (v, k) => (k + 1) / 10),
            axisLabel: {
              //X轴刻度配置
              interval: 5
            },
            axisLine: {
              symbol: ["none", "arrow"], // 分别表示起始位置和终止位置是否使用箭头，默认不使用
              symbolSize: [5, 10] // 箭头的长宽
            }
          }
        ],
        yAxis: {
          name: "Ur/Us",
          nameLocation: "end",
          nameGap: 15,
          type: "value"
        },
        tooltip: {
          trigger: "item",
          axisPointer: {
            type: "shadow"
          },
          formatter: "x={b}<br>y={c}"
        },
        smooth: true,
        series: [
          {
            name: "Ur/Us",
            data: data.map(i => this.R / i),
            type: "line",
            areaStyle: {},
            itemStyle: {
              normal: {
                opacity: 0.8
              }
            }
          }
        ]
      }; // 使用刚指定的配置项和数据显示图表。
      let op2 = {
        title: {
          text: "相频特性",
          x: "center"
        },
        xAxis: [
          {
            name: "η",
            nameLocation: "end",
            nameGap: 15,
            type: "category",
            boundaryGap: false,
            data: Array.from({ length: 200 }, (v, k) => (k + 1) / 100),
            axisLabel: {
              //X轴刻度配置
              // interval: 5
            },
            axisLine: {
              symbol: ["none", "arrow"], // 分别表示起始位置和终止位置是否使用箭头，默认不使用
              symbolSize: [5, 10] // 箭头的长宽
            }
          }
        ],
        yAxis: {
          name: "Φ(jη)",
          nameLocation: "end",
          nameGap: 15,
          type: "value"
        },
        tooltip: {
          trigger: "item",
          axisPointer: {
            type: "shadow"
          },
          formatter: "x={b}<br>y={c}"
        },
        smooth: true,
        series: [
          {
            name: "Φ(jη)",
            data: Array.from(
              { length: 200 },
              (v, k) =>
                (-Math.atan(Q * ((k + 1) / 100 - 100 / (k + 1))) * 180) / Math.PI
            ),
            type: "line",
            areaStyle: {},
            itemStyle: {
              normal: {
                opacity: 0.8
              }
            }
          }
        ]
      }; // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(op, true);
      myChart1.setOption(op1, true);
      myChart2.setOption(op2, true);
    }
  }
};
</script>